import React from 'react';
import './UserInfo.css'

const UserInfo = ({ user }) => {

    if (!user || !user.userInfos) {
        return <p>Les donnes utilisateur ne sont pas disponibles.</p>;
    }

    return (
        <div className='welcome-username'>
            <h2>Bonjour
                <span> {user.userInfos.firstName}</span>
            </h2>
            <p>Flicitations ! Vous avez explos vos objectifs hier </p>
        </div>
    );
};

export default UserInfo;



